import { useState } from 'react';
import api from '../services/api';

const Registration = () => {
  const [formData, setFormData] = useState({
    name: '',
    age: '',
    email: '',
    phone: '',
    country: '',
    category: 'MS'
  });
  const [message, setMessage] = useState('');
  const [messageType, setMessageType] = useState<'success' | 'error'>('success');
  const [loading, setLoading] = useState(false);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const submit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setMessage('');

    try {
      if (!formData.name.trim()) {
        setMessage('Name is required');
        setMessageType('error');
        setLoading(false);
        return;
      }

      if (!formData.age) {
        setMessage('Age is required');
        setMessageType('error');
        setLoading(false);
        return;
      }

      const payload = {
        name: formData.name.trim(),
        age: parseInt(formData.age),
        email: formData.email.trim() || null,
        phone: formData.phone.trim() || null,
        country: formData.country.trim() || null,
        category: formData.category
      };

      const res = await api.post('/players/', payload);
      console.log('Created player:', res.data);
      setMessage(`Player ${res.data.name} registered successfully!`);
      setMessageType('success');

      setFormData({
        name: '',
        age: '',
        email: '',
        phone: '',
        country: '',
        category: 'MS'
      });

    } catch (err: any) {
      console.error('Registration error:', err);
      const detail = err?.response?.data || err?.message || 'Network Error';
      setMessage(typeof detail === 'string' ? detail : JSON.stringify(detail));
      setMessageType('error');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="p-4 fade-in">
      <div className="card" style={{ maxWidth: '800px', margin: '0 auto' }}>
        <div className="card-header">
          <h2 className="mb-0 text-center"> Player Registration</h2>
          <p className="text-center text-muted mb-0 mt-2">
            Register new players for the tournament
          </p>
        </div>
        
        <div className="card-body">
          <form onSubmit={submit}>
            <div className="grid grid-cols-2" style={{ gap: '1.5rem' }}>
              <div className="form-group">
                <label className="form-label">
                  Full Name <span style={{ color: 'var(--error)' }}>*</span>
                </label>
                <input
                  type="text"
                  name="name"
                  value={formData.name}
                  onChange={handleInputChange}
                  required
                  className="form-input"
                  placeholder="Enter full name"
                />
              </div>

              <div className="form-group">
                <label className="form-label">
                  Age <span style={{ color: 'var(--error)' }}>*</span>
                </label>
                <input
                  type="number"
                  name="age"
                  value={formData.age}
                  onChange={handleInputChange}
                  min="8"
                  max="100"
                  required
                  className="form-input"
                  placeholder="Enter age"
                />
              </div>

              <div className="form-group">
                <label className="form-label">Email</label>
                <input
                  type="email"
                  name="email"
                  value={formData.email}
                  onChange={handleInputChange}
                  className="form-input"
                  placeholder="Enter email (optional)"
                />
              </div>

              <div className="form-group">
                <label className="form-label">Phone</label>
                <input
                  type="tel"
                  name="phone"
                  value={formData.phone}
                  onChange={handleInputChange}
                  className="form-input"
                  placeholder="Enter phone (optional)"
                />
              </div>

              <div className="form-group">
                <label className="form-label">Country</label>
                <input
                  type="text"
                  name="country"
                  value={formData.country}
                  onChange={handleInputChange}
                  className="form-input"
                  placeholder="Enter country (optional)"
                />
              </div>

              <div className="form-group">
                <label className="form-label">
                  Category <span style={{ color: 'var(--error)' }}>*</span>
                </label>
                <select
                  name="category"
                  value={formData.category}
                  onChange={handleInputChange}
                  className="form-select"
                >
                  <option value="MS"> Men Singles</option>
                  <option value="WS"> Women Singles</option>
                  <option value="MD"> Men Doubles</option>
                  <option value="WD"> Women Doubles</option>
                  <option value="XD"> Mixed Doubles</option>
                </select>
              </div>
            </div>

            <div className="text-center mt-4">
              <button
                type="submit"
                disabled={loading}
                className="btn"
                style={{ minWidth: '200px' }}
              >
                {loading ? ' Registering...' : ' Register Player'}
              </button>
            </div>

            {message && (
              <div className={messageType === 'success' ? 'success' : 'error'} style={{ marginTop: '1.5rem' }}>
                <span style={{ fontSize: '1.25rem', marginRight: '0.5rem' }}>
                  {messageType === 'success' ? '' : ''}
                </span>
                {message}
              </div>
            )}
          </form>
        </div>
      </div>
    </div>
  );
};

export default Registration;